import React, { useEffect, useState } from 'react'
import SalaryTemplateTable from './SalaryTemplateTable'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { payrollActions } from '../../../core/resource/actions'
import { pagination } from '../../../utils/Constants.config'
const SalaryTemplates = () => {
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [currentPage, setCurrentPage] = useState(1)
  const [allSalaryTemplates,satAllSalaryTemplates]=useState('')
  const navigateSalaryTempModal = (id) => {
    navigate(`/salary-template/create-salary-template${id ? `/${id}` : ''}`);
  }

  const {
    getSalaryTemplatePending,
    getSalaryTemplateRes,
    getSalaryTemplateError,

  } = useSelector(state => state.payroll)

  /* Get Salary Template Data */
  useEffect(() => {
    if (!getSalaryTemplatePending) {
      if (getSalaryTemplateError) {
        return;
      }
      if (getSalaryTemplateRes) {
        if (getSalaryTemplateRes?.code === 200) {
          satAllSalaryTemplates(getSalaryTemplateRes?.data)
        }
      }
    }
  }, [
    getSalaryTemplateRes,
    getSalaryTemplateError,
    getSalaryTemplatePending
  ])

  useEffect(() => {
    const payload = {
      page: currentPage,
      limit: pagination.limit,
    }
    dispatch(payrollActions.getSalaryTemplateRequest(payload))
  }, [currentPage])
  return (
    <div>

      <SalaryTemplateTable
        navigateSalaryTempModal={navigateSalaryTempModal}
        setCurrentPage={setCurrentPage}
        currentPage={currentPage}
        allSalaryTemplates={allSalaryTemplates}
        isLoading={getSalaryTemplatePending}
      />
    </div>
  )
}

export default SalaryTemplates
