import React, { useEffect, useMemo, useState } from 'react'
import { CustomTable } from '../../reuseableComponents'
import { getBadgeColor } from '../../../utils/common'
import { CustomButton } from '../../globalComponents'
import moment from 'moment'
import { useCustomTranslation } from '../../../translation'


const SalaryTemplateTable = (props) => {
    const { t } = useCustomTranslation()
    const {
        isLoading,
        toggleSalaryTemplateDeleteModal,
        currentPage,
        setCurrentPage,
        allSalaryTemplates,
        navigateSalaryTempModal
    } = props
    const [salaryTemplateList, setSalaryTemplateList] = useState('')


    useEffect(() => {
        setSalaryTemplateList(allSalaryTemplates)
    }, [allSalaryTemplates])


    const scopedSlots = useMemo(() => ({
        sn: (_, index) => (
            <span>{index+1}</span>
        ),
        is_active: item => (
            <span
                className={`badge ${getBadgeColor(item.is_active ? "Active" : "Inactive")}`}
            >
                {item.is_active ? "Active" : "Inactive"}
            </span>
        ),
       
        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <div className="btn-group actionDrp">
            <button type="button" className="btn dropdown-toggle-split"
                data-bs-toggle="dropdown" aria-expanded="false">
                <i className="bi bi-three-dots-vertical"></i>
            </button>
            <ul className="dropdown-menu">
                <li>
                    <span className="dropdown-item"
                        onClick={() => navigateSalaryTempModal(item?._id)}
                    >
                        Update</span>
                </li>
            </ul>
        </div>
        )
    }), [salaryTemplateList]);

    const fields = [
        { key: 'sn', label: 'S.N' },
        { key: 'template_name', label: 'Salary Template' },
        { key: 'description', label: 'Description ' },
        { key: 'is_active', label: 'Status' },
        { key: 'created_at', label: 'Created At' },
        { key: 'action', label: 'Action' }
    ];

    return (
        <div className='cardBox myRequest mb-3 employeeHistory'>
            <div className="d-flex align-items-center justify-content-between gap-3">
                <div className="cardHead d-flex">
                    <img src="images/myrequest-icon.svg" alt="" width={35} />
                    <h3 className="ms-2 mt-2">{t("headings.salaryTemplate")}</h3>
                </div>
                <CustomButton
                    text={"Add Salary Template"}
                    classNames={"btn btn-primary"}
                    handleClick={() => navigateSalaryTempModal()}
                />
            </div>
            <CustomTable
                data={salaryTemplateList?.data}
                fields={fields}
                isLoading={isLoading}
                pagination={true}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                totalPages={salaryTemplateList?.totalPages}
                isHeader={false}
                tableStyle={{ padding: "0px", boxShadow: "none" }}
                scopedSlots={scopedSlots}
            />
        </div>
    )
}

export default SalaryTemplateTable
